import Button from '@restart/ui/esm/Button';
import React from 'react'
import { Container} from 'reactstrap';

export const Cartoes = ()  => {
    return (
            <Container className="information" id="about">
            <div class="container-fluid">  
                <div class="row text-light">
                    <div class="col-lg-4 text-center p-5" data-aos="zoom-in">
                        <i class="fas fa-tachometer-alt fa-3x p-2"></i>
                        <h4 class="py-3">GCONNECTWAY</h4>
                        <p class="para-light">é o seu ecossistema de saúde, uma aplicação completa para as jornadas do paciente
                                            , hospital e do médico. Composto por aplicações inteligentes como: 
                                            GMARKING a forma de realizar agendamentos e gerenciar as agendas de médicos
                                            , serviços e equipamentos de saúde. GFAST um conceito de atendimento 
                                            rápido, considerando todos os principais atores do processo, 
                                            como operadora de convênios, paciente e seus dados e prestadora de 
                                            serviços.</p>
                                            <br/>
                                            <Button>Saiba mais</Button>
                                            
                    </div>
                    <div class="col-lg-4 text-center p-5"  data-aos="zoom-in">
                        <i class="fas fa-clock fa-3x p-2"></i>
                        <h4 class="py-3">GPULSE</h4>
                        <p class="para-light"> é o prontuário inteligente do seu paciente, 
                                dentro da unidade de saúde, considerando os principais pontos de assistência 
                                e como alvo principal a agilidade das informações e a fácil visualização 
                                dos dados do paciente.</p>
                                <br/>
                                <Button>Saiba mais</Button>
                    </div>
                    <div class="col-lg-4 text-center p-5 text-dark"  data-aos="zoom-in"> 
                        <i class="fas fa-clock fa-3x p-2"></i>
                        <h4 class="py-3">GHEALTHpay</h4>
                        <p class="para-light"> emponderando o médico para as melhores tomadas de decisão 
                                financeira e a sua gestão inteligente do seu dinheiro.</p><br/>
                                <Button>Saiba mais</Button>
                    </div>
                </div>
            </div> 
            </Container> 

);
}