import React from 'react'
import { Link } from 'react-router-dom';
import {Menu} from '../../componets/menu'
import { Titulo, ButtonImpacto, Container, BotaoAcao, ConteudoTitulo } from '../../styles/custom_adm';
import { library } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { fas } from '@fortawesome/free-solid-svg-icons';
library.add(fas);

export const hospital = ()  => {
    return (
            <Container>
                    <Menu></Menu>
                    <Titulo>Acesso Hospital</Titulo>
                    <ConteudoTitulo>
                    
                        <BotaoAcao><Link to={"#"}><ButtonImpacto onClick={() => "#"} type="button"> .:: <FontAwesomeIcon icon="user-injured"/> Prontuário do Paciente ::..</ButtonImpacto></Link></BotaoAcao>
                        <BotaoAcao><Link to={"#"}><ButtonImpacto onClick={() => "#"} type="button">..:: <FontAwesomeIcon icon="ambulance"/> GCONNECTWAY ::..</ButtonImpacto></Link></BotaoAcao>
                        <BotaoAcao><Link to={"/"}><ButtonImpacto onClick={() => "#"} type="button">..:: <FontAwesomeIcon icon="angle-double-left"/> voltar ::..</ButtonImpacto></Link></BotaoAcao>



                    </ConteudoTitulo>

            </Container>
            );
        }